import {NgModule} from '@angular/core';

import {Routes, RouterModule} from '@angular/router';
import {ReportReaderComponent} from './report-reader.component';
import {HeroListComponent} from './report-reader-list/report-reader-list.component';
//import {HeroCreateComponent} from './hero-create/hero-create.component';
//import {HeroDetailComponent} from './hero-detail/hero-detail.component';
//import {HeroEditComponent} from './hero-edit/hero-edit.component';

export const reportReaderRoutes: Routes = <Routes>[{
  path: '',
  component: ReportReaderComponent,
  children: [
    {path: '', component: HeroListComponent},
	/*
    {path: 'detail/:id', component: HeroDetailComponent},
    {path: 'create', component: HeroCreateComponent},
    {path: 'edit/:id', component: HeroEditComponent}
	*/
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(reportReaderRoutes)],
  exports: [RouterModule]
})
export class ReportReaderRoutingModule {
}

export const reportReaderRoutedComponents = [
  ReportReaderComponent,
  HeroListComponent
  
];



